body{color:#333;font-size:16px;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen,Ubuntu,Cantarell,"Open Sans","Helvetica Neue",sans-serif}*{box-sizing:border-box}.playground{height:100%;max-width:1200px;margin:0 auto}.playground__header{display:flex;justify-content:center;align-items:center;height:100px;border-bottom:1px solid #efefef}.playground__content{position:relative;min-height:60vh;padding:2rem}.v-dialogs{display:none;position:absolute;top:0;left:0;width:100%;height:100%;background-color:rgba(0,0,0,.3)}.v-dialogs--with-dialogs{display:flex;justify-content:center;align-items:center}.v-dialogs .v-dialog{min-width:260px;background-color:#fff;padding:1rem}